  padding-left: 3rem;
}

label .text p {
  padding-bottom: 0.5rem;
  margin-left: 2rem;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  color: #fff;
  font-weight: bolder;
  text-align: left;
  font-size: 0.9vw;
  text-transform: uppercase;
  position: relative;
}

label .text p::before {
  content: '';
  position: absolute;
  width: 5px;
  height: 5px;
  background: #fcbf1e;
  border-radius: 50%;
  top: 50%;
  -webkit-transform: translateY(-100%);
