}

label a {
  border-radius: 4px;
  position: absolute;
  font-family: 'Roboto Mono';
  bottom: 5%;
  right: 10%;
  text-decoration: none;
  color: #fcbf1e;
  background: rgba(0, 0, 0, 0.7);
  padding: 1rem 1.5rem;
  border: 3px solid #fcbf1e;
  font-size: 1.4vw;
  text-align: center;
  font-weight: 400;
  margin-bottom: 1rem;
  transition: border-color 0.5s ease, color 0.5s ease;
  z-index: 1;
}
label a:hover {
  border: 3px solid #303960;
  color: #1b6ca8;
}
label a:hover .swipehover {
  width: 11.8vw;
}
.swipehover {
  border-radius: 4px;
  transition: width 0.5s ease;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  position: absolute;
  bottom: 5%;
  right: 10.1%;
  width: 11.8vw;
  width: 0vw;
  height: 50px;
  font-size: 1.4vw;
  background: red;
  z-index: 0;
  margin-bottom: 1rem;
}
[type='radio'] {
  display: none;
}

